<template>
  <transition name="toast-fade">
    <div
      class="toast"
      :class="toastClass"
      v-show="isShow"
      @mouseenter="onMouseenter"
      @mouseleave="onMouseleave"
    >
      <!-- <button class="toast-close-button" @click="hide">×</button> -->
      <div class="toast-container">
        <div class="toast-title">{{title}}</div>
        <div class="toast-content">{{content}}</div>
      </div>
    </div>
  </transition>
</template>
<script>
export default {
  data: () => ({
    // list: [],
    title: null, //toast标题
    content: null, //toast内容
    type: null, //toast类型
    isShow: false, //toast是否显示
    timer: null,
    onShow: () => {},
    onHide: () => {}
  }),
  computed: {
    // 样式'success, error, warning, default'
    toastClass() {
      return this.type ? "toast-" + this.type : null;
    }
  },
  methods: {
    // 显示
    show(params) {
      let { content, title, onShow, onHide, type } = params;
      this.type = type;
      this.content = content;
      this.title = title;
      this.onShow = onShow;
      this.onHide = onHide;
      this.isShow = true;
      this.setTimer();
      setTimeout(() => {
        this.isShow = false;
      }, 3000); //3s后自动关闭
    },

    // 隐藏
    hide() {
      this.isShow = false;
    },
    // 计时器
    setTimer() {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        this.isShow = false;
      }, 4000);
    },
    // 鼠标移至组件时保持显示状态
    onMouseenter() {
      clearTimeout(this.timer);
    },
    // 鼠标移开组件时重新定时
    onMouseleave() {
      if (this.isShow) this.setTimer();
    }
  },
  watch: {
    isShow(val) {
      if (val && typeof this.onShow === "function") {
        this.onShow();
      } else if (!val && typeof this.onHide === "function") {
        this.onHide();
      }
    }
  }
};
</script>
<style>
.toast {
  position: fixed;
  top: 50%;
  left: 40%;
  /* transform: translate(-50%, -50%); */
  
  display: block;
  overflow: hidden;
  
  box-shadow: 0 0 0.06rem #999;
  opacity: 0.8;
  border-radius: 0.2rem;
  padding: 0.15rem 0.15rem 0.15rem 0.15rem;
  background-position: 0.15rem center;
  background-repeat: no-repeat;
  color: #333;
  background-color: #f0f3f4;
}

.toast-success {
  color: #fff;
  background-color: #000;
  padding: 0.15rem 0.15rem 0.15rem 0.5rem;
  /* background-image: url("data:image/png;base64,") !important; */
}

.toast-error {
  color: #fff;
  background-color: #ff0000;
  padding: 0.15rem 0.15rem 0.15rem 0.5rem;
  background-image: url("data:image/png;base64,") !important;
}

.toast-warning {
  color: #fff;
  background-color: #cd853f;
  padding: 0.15rem 0.15rem 0.15rem 0.5rem;
  background-image: url("data:image/png;base64,") !important;
}

.toast:hover {
  opacity: 1;
  box-shadow: 0 0 0.18rem #888;
  transition: all 200ms ease;
}

.toast-container {
  vertical-align: middle;
}

.toast-fade-enter,
.toast-fade-leave-active {
  opacity: 0;
  transform: translateX(100%);
}

.toast-fade-leave-active,
.toast-fade-enter-active {
  transition: all 400ms cubic-bezier(0.36, 0.66, 0.04, 1);
}

.toast-title {
  font-size: 0.14rem;
  font-weight: bold;
}

.toast-close-button {
  padding: 0.02rem 0.02rem;
  border: none;
  background: transparent;
  position: relative;
  right: -0.1rem;
  top: -0.15rem;
  float: right;
  font-size: 0.2rem;
  font-weight: bold;
  color: #ffffff;
  -webkit-text-shadow: 0 0.01rem 0 #ffffff;
  text-shadow: 0 0.01rem 0 #ffffff;
  -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=80);
  filter: alpha(opacity=80);
}
</style>


